    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.3.	Estructura del itinerario y servicios asociados

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      .col-md-9.mb-4.mb-md-0.my-2
        p La estructura del itinerario turístico constituye la base operativa de todo recorrido o programa de viaje. Un itinerario bien diseñado permite organizar el tiempo, las actividades, los desplazamientos y los servicios de manera ordenada y eficiente, asegurando el cumplimiento de los objetivos del recorrido y la satisfacción del visitante.
        p De acuerdo con la Organización Mundial del Turismo (OMT, 2022), “el itinerario es la herramienta que transforma la planificación en experiencia, al convertir la logística en vivencias memorables para el turista”. Por tanto, su elaboración requiere precisión técnica y sensibilidad hacia las expectativas del usuario y las características del destino.
        p Un itinerario no es simplemente una lista de lugares a visitar; es una secuencia lógica y temporal de experiencias, respaldada por una estructura clara que incluye: horarios, medios de transporte, actividades, tiempos de descanso, alimentación, visitas guiadas, alojamiento y contingencias. En otras palabras, el itinerario es el documento que orienta el desarrollo del recorrido turístico, garantizando que cada momento del viaje aporte valor, seguridad y coherencia.
        
      .col-md-3
        figure
          img(src='@/assets/curso/temas/tema1/tema1/tema1-img4.svg', alt='El diseño y la organización de paquetes turísticos')

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      h3 Elementos básicos de un itinerario
      p La estructura de un itinerario turístico se compone de varios elementos fundamentales que permiten su correcta ejecución:

    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      .col-md-12
        figure
          img(src='@/assets/curso/temas/tema1/tema3/rec7-img.svg', alt='El diseño y la organización de paquetes turísticos')

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      h3 Servicios asociados
      p Los servicios asociados son aquellos que complementan el desarrollo del itinerario y garantizan la comodidad, la seguridad y la satisfacción del turista. Entre los más importantes se encuentran:

      .col-md-11
        .p • Alojamiento: debe seleccionarse según la categoría del recorrido, el presupuesto y la ubicación estratégica.
        .p • Alimentación: debe contemplar la duración del recorrido, las costumbres culturales y las necesidades dietarias del grupo.
        .p • Transporte: su elección depende del tipo de terreno, la distancia y el número de usuarios.
        .p • Guianza e interpretación: fundamentales para contextualizar el recorrido y generar aprendizajes significativos.
        .p  • Seguros y asistencia médica: obligatorios en todo programa turístico, especialmente en actividades de naturaleza o aventura.
        .p • Entradas, permisos y tasas: necesarios en áreas naturales, sitios patrimoniales o comunidades locales.
        
